import song from '@/components/song/index.vue'

export default {
	data() {
		return {
			songs:[],
			dishs:[],
			offset:0,
			flag:false
		};
	},
	onLoad() {
		this.newsongs()
		this.dish()
	},
	onReachBottom() {
		if(this.flag){
			this.dish()
		}
	},
	components: {song},
	methods:{
		newsongs(){
			this.$request({
				url:'/personalized/newsong'
			}).then((data) => {
				if(data.data.code == 200){
					this.songs = data.data.result
					console.log('推荐歌曲',this.songs)
				}
			})
		},
		getsong(id){
			uni.navigateTo({
				url: `../music/music?id=`+id
			})
		},
		getsong1(id){
			// uni.setStorageSync('playlist',this.songs)
			let songs = this.songs
			songs = songs.map((item)=>{
				return {
					...item,
					hhh:'1111',
					al:{
						picUrl:item.picUrl,
						
					}
				}
			})
			songs = {
				tracks:[...songs]
			}
			console.log(songs)
			uni.setStorageSync('playlist',songs)
			uni.navigateTo({
				url: `../music/music?id=`+id
			})
		},
		getsong2(id){
			// uni.setStorageSync('playlist',this.songs)
			let songs = this.dishs
			songs = songs.map((item)=>{
				return {
					...item,
					hhh:'1111',
					al:{
						picUrl:item.picUrl,
						
					}
				}
			})
			songs = {
				tracks:[...songs]
			}
			console.log(songs)
			uni.setStorageSync('playlist',songs)
			uni.navigateTo({
				url: `../music/music?id=`+id
			})
		},
		dish(){
			this.$request({
				url:"/top/album?offset="+this.offset+"&limit=8"
			}).then((data)=>{
				if(data.data.code == 200){
					let dishs = data.data.albums
					if(dishs.length >= 8){
						this.flag = true
					}else{
						this.flag = false
					}
					this.offset = this.offset + 8
					dishs = dishs.map((item)=>{
						return {
							id : item.id,
							picUrl : item.picUrl,
							name : item.name,
							song : {
								artists:[{
									...item.artists[0]
								}]
							}
						}
					})
					this.dishs.push(...dishs)
					console.log('推荐歌单',dishs)
				}
			})
		},
	}
}
